import React, { useEffect, useState } from "react";
import { navigate } from "gatsby";
import "../../shared/ProtectedRoute/protected.css"; // reuse same branded layout
import { handleLogout } from "../../../database/functions";

const i18n = {
  EN: {
    brandTitle: "TINH HOA VIỆT",
    tagline: "Where Vietnamese Excellence Meets Global Recognition",
    loggingOut: "Logging you out securely...",
    success: "✅ You have been logged out successfully.",
    fail: "❌ Logout failed. Please try again.",
  },
  VN: {
    brandTitle: "TINH HOA VIỆT",
    tagline: "Nơi tôn vinh tinh hoa Việt Nam vươn tầm quốc tế",
    loggingOut: "Đang đăng xuất an toàn...",
    success: "✅ Bạn đã đăng xuất thành công.",
    fail: "❌ Đăng xuất thất bại. Vui lòng thử lại.",
  },
};

const Logout: React.FC = () => {
  const [status, setStatus] = useState(i18n.EN.loggingOut);
  const [language] = useState<"EN" | "VN">("EN"); // optional toggle if needed
  const t = i18n[language];

  useEffect(() => {
    const onSuccess = () => {
      setStatus(t.success);
      setTimeout(() => navigate("/"), 1500);
    };

    const onFail = () => {
      setStatus(t.fail);
    };

    // Run logout immediately
    handleLogout({ onSuccess, onFail });
  }, [t.success, t.fail]);

  return (
    <div className="auth-shell">
      <aside className="brand-side" aria-hidden>
        <div className="brand-sheen" />
        <div className="brand-pattern" />
        <div className="brand-wrap">
          <h1 className="brand-name">{t.brandTitle}</h1>
          <p className="brand-tag">{t.tagline}</p>
        </div>
      </aside>

      <main className="form-side">
        <div className="loading-block">
          <div className="spinner" />
          <p className="loading-text">{status}</p>
        </div>
      </main>
    </div>
  );
};

export default Logout;
